import Vue from 'vue'
import Router from 'vue-router'
import head from '@/components/head'
import login from '@/components/login'
import cop from '@/components/cop'
import matchZb from '@/components/matchZb'
import index from '@/components/choice/index'
import data from '@/components/choice/data'
import match from '@/components/choice/match'
import appYa from '@/components/choice/appYa'

import newsShow from '@/components/choice/newsShow'

import teamInfo from '@/components/teamInfo'
import playerInfo from '@/components/playerInfo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'head',
      component: head, 
      redirect:{name:'index'},
      children:[
        {path:"/index",name:'index',component:index,},
        {path:'/newsShow',name:'newsShow',component:newsShow},
        {path:"/data",name:'data',component:data},
        {path:"/match",name:'match',component:match},
        {path:"/app",name:'appYa',component:appYa},
        {path:"/login",name:'login',component:login},
        {path:"/cop",name:'cop',component:cop},
        {path:"/teamInfo",name:'teamInfo',component:teamInfo},
        {path:"/playerInfo",name:'playerInfo',component:playerInfo},
        {path:"/matchZb",name:'matchZb',component:matchZb},
      ]
    },
    
    
   
  ]
})



