<template>
  <div class="newShow">
    <div class="newLeft">
      <div class="leftTop">
        <div class="dqp">
          <span>懂球帝首页 ></span>
          <span>懂球号文章</span>
        </div>
        <h2 v-if='title&&title!=""'>{{title}}</h2>
        <div class="leftTopBot">
          <span>{{infoYa.posterScreenName}}</span>
          <span class="tn">{{infoYa.publishDateStr}}</span>
        </div>
      </div>
      <div class="leftBot">
        <p>{{infoYa.content}}</p>
        <div v-for="(img,index) in infoYa.imageUrls" :key="index">
          <img :src="img" alt class="imgT">
        </div>
        <p class="p1">以上仅代表作者观点，并非懂球帝官方观点</p>
        <p class="p1">
          自媒体如有侵权，请联系
          <span>懂球帝</span>
        </p>

        <span class="share">分享到:</span>
        <a href="###" class="a1">微博</a>
        <a href="###" class="a1">朋友圈</a>
        <a href="###" class="a1">QQ空间</a>
        <a href="###" class="a1">贴吧</a>
      </div>
    </div>
    <div class="newRight">
      <div class="rightHead">
        <span class="relatB">相关标签</span>
        <ul>
          <li v-for='(site,index) in bqs' :key='index'>
      
            <div class="rightName">
              <p @click='goInfo(site)'>{{site}}</p>
            </div>
          </li>
         
        </ul>
      </div>
      <div class="rightHead head2">
        <span class="relatB">相关新闻</span>
        <ul>
          <li class="li1" @click='go(sb1)'>
            <p class="pTitle">{{info[sb1].title}}</p>

            <span class="tn">{{info[sb1].publishDateStr}}</span>
          </li>
          <li class="li1" @click='go(sb)'>
            <p class="pTitle">{{info[sb].title}}</p>

            <span class="tn">{{info[sb].publishDateStr}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import jun from "../../../static/img/jun.png";
import jlt from "../../../static/img/jlt.png";
export default {
  name: "newShow",
    inject:['reload'],
  data() {
    return {
      teamMc:"",
      jun,
      id: "",
      infoYa: "",
      info: "",
      title:"",
      bqs:[],
      sb:Math.floor(Math.random()*8+1),
      sb1:Math.floor(Math.random()*8+1),
    };
  },

  created() {
        let oneY = Math.floor(Math.random()*16+1)
          let twoY = Math.floor(Math.random()*16+1)
          let threY = Math.floor(Math.random()*16+1)
          let forY = Math.floor(Math.random()*16+1)
          let index=0;
       if(this.$store.state.active1==0){
         this.teamMc = this.$store.state.teamMc.xj
         for(var k in this.teamMc){
          if(index == oneY || index==twoY || threY == index || index==forY){
            this.bqs.push(this.teamMc[k].name);
         }
         index++;
         }
    
       } 
         if(this.$store.state.active1==1){
         
             this.teamMc = this.$store.state.teamMc.yc
                 for(var k in this.teamMc){
          if(index == oneY || index==twoY || threY == index || index==forY){
            this.bqs.push(this.teamMc[k].name);
         }
         index++;
         }
       } 
         if(this.$store.state.active1==2){
             this.teamMc = this.$store.state.teamMc.yj
                 for(var k in this.teamMc){
          if(index == oneY || index==twoY || threY == index || index==forY){
            this.bqs.push(this.teamMc[k].name);
         }
         index++;
         }
       } 
         if(this.$store.state.active1==3){
             this.teamMc = this.$store.state.teamMc.dj
                 for(var k in this.teamMc){
          if(index == oneY || index==twoY || threY == index || index==forY){
            this.bqs.push(this.teamMc[k].name);
         }
         index++;
         }
       } 
         if(this.$store.state.active1==4){
             this.teamMc = this.$store.state.teamMc.zc
                 for(var k in this.teamMc){
          if(index == oneY || index==twoY || threY == index || index==forY){
            this.bqs.push(this.teamMc[k].name);
         }
         index++;
         }
       } 
    this.id = this.$store.state.id;
    this.infoYa = this.$store.state.infoYa[this.id];

    this.title = this.infoYa.title;
    this.getNews();
  },
 
  methods: {
    goInfo(name){
      this.$store.state.teamName = name;
      this.$router.push({name:"teamInfo"})
    },
    getNews() {
      this.$http({
        method: "get",
        url:
          "/api//post/qqsport?baid="+this.$store.state.bigIndex+"&apikey=MUPq1AI0WoZyou8tEoy8xaZe278eEJtYzjtItFLQGxYIMPT4eysoiS4XBs9EJY9d"
      }).then(res => {
        this.info = res.data.data;
           this.info.splice(this.$store.state.id,1);
      
      });
    },
    go(index){
        console.log(124);
       
        this.$store.state.id = index
        var sb = Math.floor(Math.random()*8+1);
         var sb1 = Math.floor(Math.random()*8+1);
        this.$store.state.infoYa =  this.info;
        this.reload();
    }
  }
};
</script>
<style scoped>
.newShow {
  width: 1000px;
  margin: 0 auto;
  margin-top: 100px;
  overflow: hidden;
}
.newLeft {
  float: left;
  width: 640px;
  overflow: hidden;
}
.leftTop span {
  font-size: 12px;
}
h2 {
  margin-top: 30px;
}
.leftTopBot {
  margin-top: 30px;
}
.tn {
  color: #999;
}
.leftTop {
  border-bottom: 1px solid #e6eaed;
  padding-bottom: 30px;
}
.leftBot {
  padding-top: 20px;
}
.imgT {
  display: block;
  margin: 10px auto;
  max-width: 96%;
}
.leftBot p {
  padding: 10px 0;
  text-indent: 2em;
  font-size: 16px;
  line-height: 1.7;
}
.leftBot .p1 {
  font-size: 12px;
  color: #999;
}
.p1 span {
  color: #666;
}
.share {
  margin-top: 20px;
  margin-left: 15px;
  font-size: 14px;
}
.a1 {
  font-size: 12px;
  display: inline-block;
  margin-left: 10px;
  width: 24px;
  height: 24px;
  background: red;
  vertical-align: middle;
}
.newRight {
  float: right;
  width: 300px;
  overflow: hidden;
}
.rightHead ul {
  border-top: 1px solid #16b13a;
}
.rightHead img {
  width: 36px;
  vertical-align: middle;
  float: left;
}
.relatB {
  display: inline-block;
  padding: 0 15px;
  color: white;
  background: #16b13a;
  border-bottom: 1px solid #16b13a;
  line-height: 28px;
}
.rightHead li {
  overflow: hidden;
  line-height: 40px;
  padding: 10px 0;
  border-bottom: 1px solid #e6eaed;
  text-indent: 10px;
}
.rightName {
  float: left;
}
.rightHead p {
  font-size: 14px;
  line-height: 20px;
}
.p2 {
  color: #999;
}
.head2 {
  margin-top: 50px;
}
.pTitle{
    font-size: 14px;
}
.head2 .li1 {
    cursor: pointer;
  font-size: 14px;
  text-align: left;
}
</style>



